@import '../../global.scss';

.intro{
    background-color: white;

    
    
    position: sticky;

    @include mobile{
      
        
    }
   
    .right{
        // flex: 0.5;
        margin-bottom:10%;
        margin-top: 9%;

        @include mobile{
            margin-top: 20%;

        }

        .wrapper{
            width: 100%;
            height: 100%;
            padding-left: 250px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            @include mobile{
                padding: 0px;
                align-items: center;
                flex-direction: column;
            justify-content: center;

            }

            h1{ font-size: 65px;
            margin: 10px 0;
            
            @include mobile{
                font-size: 50px;
            }
              }

              .newH1{
                font-size: 65px;
                margin: 10px 0;
                color: #2b2926;
                @include mobile{
                    padding-right: 4px;
                    font-size: 45px;
                    text-align: center;
                }
              }
        
            h2{font-size: 30px;
                @include mobile{
                    font-size: 23px;
                }}
            h3{font-size: 30px;
                span{
                    font-size: 35px;
                    color: rgb(108, 108, 247);
                    @include mobile{
                        font-size: 28px;
                    }
                }
            }
            button{
                
                width: 280px;
                margin-top: 20px;
                height: 46px;
                background-color:white;
                color: $mainColor;
                font-size: 18px;
                font-weight: 600;
                border: 0;
                border-radius: 10px;
                border: 2px solid $mainColor;
                transition: all 0.5s ease;

                &:hover{
                    color: white;
                border: 2px solid white;

                    background-color: rgb(108, 108, 247);
                }
            }
        }
    }
    }
